import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { BrowserMultiFormatReader, Result } from '@zxing/library';

@Component({
  selector: 'app-product-scanner',
  templateUrl: './product-scanner.component.html',
  styleUrls: ['./product-scanner.component.scss'],
})
export class ProductScannerComponent implements OnInit {
  isScanning = false;
  isProcessing: boolean;
  scanner: BrowserMultiFormatReader;
  supportedFormats = ['CODE_128', 'QR_CODE', 'CODE_39', 'EAN_13'];
  currentStream: MediaStream | null = null;
  videoElement: HTMLVideoElement;

  constructor(
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.scanner = new BrowserMultiFormatReader();
  }


  ngOnInit() {
    if (!this.scanner) {
      this.scanner = new BrowserMultiFormatReader();
    }
    this.startScan();
  }

  onBack() {
    setTimeout(() => {
      this.stopScan();
    }, 2000);
    this.router.navigate(['/tabs/stop-list/stop-detail/pod/capture-details']
      // , {
      // queryParams: {
      //   route_number: this.routeNumber,
      //   lvs_number: this.lvsNumber,
      // },
      // }
    );
  }

  stopScan() {
    if (this.videoElement) {
      this.videoElement.pause();
      this.videoElement.srcObject = null;
    }
    if (this.currentStream) {
      this.currentStream.getTracks().forEach((track) => track.stop());
      this.currentStream = null;
    }
    this.scanner.reset();
    this.isScanning = false;
    this.isProcessing = false;
  }

  async startScan() {
    this.isScanning = true;
    this.isProcessing = false;
    try {
      const devices = await navigator.mediaDevices.enumerateDevices();
      const videoDevices = devices.filter(
        (device) => device.kind === 'videoinput'
      );
      if (videoDevices.length > 0) {
        const backCamera = videoDevices.find(
          (device) =>
            device.label.toLowerCase().includes('back') ||
            device.label.toLowerCase().includes('environment')
        );
        const selectedDeviceId = backCamera
          ? backCamera.deviceId
          : videoDevices[0].deviceId;

        console.log('📱 Selected camera deviceId:', selectedDeviceId);
        const isMobile = /Android|iPhone|iPad|iPod/i.test(navigator.userAgent);

        const constraints = {
          video: {
            deviceId: selectedDeviceId,
            facingMode: isMobile ? { exact: 'environment' } : 'user',
            width: { ideal: 1280 },
            height: { ideal: 720 },
          },
        };


        this.currentStream = await navigator.mediaDevices.getUserMedia(
          constraints
        );
        console.log('✅ Camera stream started successfully');
        this.videoElement = document.querySelector('video') as HTMLVideoElement;
        this.videoElement.srcObject = this.currentStream;
        this.videoElement.play();

        this.adjustZoom(3.5);

        this.scanner.decodeFromVideoDevice(
          selectedDeviceId,
          'reader',
          (result: Result) => {
            if (this.isProcessing || !result) return;
            this.isProcessing = true;

            const decodedText = result.getText();
            console.log('📦 Barcode decoded:', decodedText);
            if (
              decodedText &&
              decodedText.length >= 1 &&
              decodedText.length <= 20
            ) {
              console.log('✅ Valid scan detected, saving to localStorage...');
              localStorage.setItem('scanned_location', decodedText);
              this.router.navigate(['/tabs/stop-list/stop-detail/pod/capture-details']);
              // this.router.navigate(['/pickup/location-confirmation'], {
              //   queryParams: {
              //     route_number: this.routeNumber,
              //     lvs_number: this.lvsNumber,
              //   },
              // });
              setTimeout(() => {
                console.log('navigate page')
                this.stopScan();
              }, 2000);
            } else {
              setTimeout(() => {
                this.stopScan();
              }, 2000);
            }
          }
        );
      } else {
        this.isScanning = false;
      }
    } catch (error) {
      this.isScanning = false;
    }
  }

  adjustZoom(event: number) {
    if (this.currentStream) {
      const videoTrack = this.currentStream.getVideoTracks()[0];
      const constraints: MediaTrackConstraints = {
        advanced: [{ zoom: event } as unknown as MediaTrackConstraintSet],
      };
      videoTrack
        .applyConstraints(constraints)
        .then(() => {
          console.log('Zoom applied successfully');
        })
        .catch((err) => {
          console.error('Error applying zoom constraints:', err);
        });
    } else {
      console.warn('No active video stream available to adjust zoom.');
    }
  }

}
